}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.float{
	position:fixed;
	width:200px;
	height:60px;
	bottom:40px;
	right:40px;
	background-color:#1f1e1e;
	color:#FFF;
	border-radius:50px;
	text-align:center;
    font-size:25px;
	box-shadow: 2px 2px 3px #999;
    z-index:100;
}
.float:hover{
	position:fixed;
	width:200px;
	height:60px;
	bottom:40px;
	right:40px;
	background-color:#8b9191;
	color:rgb(0, 0, 0);
	border-radius:50px;
	text-align:center;
    font-size:25px;
	box-shadow: 2px 2px 3px #999;
    z-index:100;
}


.my-float{
	margin-top:16px;
}

