.LocationWrapper{
    display: grid ;
    grid-template-rows: auto;
    box-sizing:content-box ;
    align-content: center;
    margin: 0;
    background-color:#595F72;
    
}
.Info{
    width: 100%;
    text-align: center;
    box-sizing:content-box ;
    align-content: center;
    padding-bottom: 10%;
    padding-left: 2%;
    font-size: 22px;
    color: #d9d9d9;
    
   
}
.mapouter{
    position:relative;
    height:auto;
    width:100%;
    padding-bottom: 15px;
    overflow: hidden;
}

#gmap_canvas {
    overflow:hidden;
    background:none!important;
    height:500px;
    width:100%;
   
}

@media screen and (max-width: 600px){

    .LocationWrapper{
        display:grid;
        grid-template-columns:auto;
    }

    .Info{
        margin: 0;
        width: 50%;
        text-align: center;
        box-sizing:content-box ;
        align-content: center;
        font-size: 22px;
        color: #d9d9d9;
        
       
    }

    #gmap_canvas {
        overflow:hidden;
        background:none!important;
        height:500px;
        width:100%;
       
    }

    .mapouter{
        position:relative;
        height:auto;
        width:100%;
        padding-bottom: 15px;
        overflow: hidden;
    }
}
.TheNavbar {
    background-color: #fff;
    opacity: 0.98;
    height: 15%;
    width: 100%;
    color: #fff ;
    position: fixed;
    z-index: 100;
    opacity: 0.99;

}
.footer-dark {
    padding:50px 0;
    color:#f0f9ff;
    background-color:#595F72;
  }
  
  .footer-dark h3 {
    margin-top:0;
    margin-bottom:12px;
    font-weight:bold;
    font-size:16px;
  }
  
  .footer-dark ul {
    padding:0;
    list-style:none;
    line-height:1.6;
    font-size:14px;
    margin-bottom:0;
  }
  
  .footer-dark ul a {
    color:inherit;
    text-decoration:none;
    opacity:0.6;
  }
  
  .footer-dark ul a:hover {
    opacity:0.8;
  }
  
  @media (max-width:767px) {
    .footer-dark .item:not(.social) {
      text-align:center;
      padding-bottom:20px;
    }
  }
  
  .footer-dark .item.text {
    margin-bottom:36px;
  }
  
  @media (max-width:767px) {
    .footer-dark .item.text {
      margin-bottom:0;
    }
  }
  
  .footer-dark .item.text p {
    opacity:0.6;
    margin-bottom:0;
  }
  
  .footer-dark .item.social {
    text-align:center;
  }
  
  @media (max-width:991px) {
    .footer-dark .item.social {
      text-align:center;
      margin-top:20px;
    }
  }
  
  .footer-dark .item.social > a {
    font-size:20px;
    width:36px;
    height:36px;
    line-height:36px;
    display:inline-block;
    text-align:center;
    border-radius:50%;
    box-shadow:0 0 0 1px rgba(255,255,255,0.4);
    margin:0 8px;
    color:#fff;
    opacity:0.75;
  }
  
  .footer-dark .item.social > a:hover {
    opacity:0.9;
  }
  
  .footer-dark .copyright {
    text-align:center;
    padding-top:24px;
    opacity:0.3;
    font-size:13px;
    margin-bottom:0;
  }
  
.CarouselWrapper{
    width: 100% ;

    background-color: #595F72;
    /* Add this */
    top:0;
    left:0;
    margin: 0;
    padding: auto;
}


img .CarouselWrapper {

    height:100%;
    width:100%
}

#TheSlider {

    width: 100%;
}

/* Contact */

#contact {
  color: white;
  background-color: #0FA3B1;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 720px;
  position: relative;
  padding-bottom: 50px;
  padding-top: 100px;
  filter: grayscale(10%);
}

.contact-item {
  margin-left: 20px;
  margin-bottom: 50px;
}

.item-link {
  display: flex;
  align-items: center;
}

.item-link i,
.item-link span {
  color: white;
  transition: all 0.3s ease;
}

.item-link:hover i,
.item-link:hover span {
  color: #096169;
}

.tm-input {
	margin: 0 0 20px 0;
	width: 90%;
  padding: 8px 20px;
  border-radius: 6px;
  border: 1px solid white;
  background: #43cfdb;
  color: rgba(0, 0, 0, 0.973);
}

.tm-btn-submit {
	font-size: 0.9em;
	color: #fff;
	background-color: #646464;
	width: 50%;
	margin-bottom: 60px;
}

.tm-btn-submit:hover {
	color: #fff;
	background-color: #e0eba9;
}

:-ms-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: white;
  opacity: 1; /* Firefox */
}

::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: white;
  opacity: 1; /* Firefox */
}

:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: white;
}

::-ms-input-placeholder {
  /* Microsoft Edge */
  color: white;
}

.tm-footer {
  position: absolute;
  bottom: 35px;
  left: 0;
  right: 0;
  padding: 0 15px;
}

.tm-footer a {
	color: #fff;
}

.tm-footer a:hover {
	color: #9CF;
}

.tm-footer-link {
  color: white;
}

.tm-footer-link:hover,
.tm-footer-link:focus {
  color: #38B;
  text-decoration: none;
}

p {
  line-height: 1.9;
}

@media (min-width: 768px) {
  .tm-intro-text-container {
    padding-left: 0px;
  }

  .navbar-expand-md .navbar-nav .nav-link {
    padding-right: 30px;
    padding-left: 30px;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1275px;
  }

  .tm-container-gallery {
    max-width: 1290px;
  }

  .tm-container-contact {
    max-width: 1063px;
  }
}

@media (max-width: 991px) {
  .tm-intro-text-container {
    padding-left: 15px;
    padding-top: 30px;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
  }

  .tm-intro-img {
    display: block;
    margin-left: auto;
    margin-right: auto;
  }

  .tm-btn-submit {
    margin-left: 0;
    margin-top: 20px;
  }
}

@media (max-width: 767px) {

  .navbar-nav {
    max-width: 200px;
    text-align: right;
  }

  .navbar-collapse {
    background-color: rgb(255, 255, 255);
    padding: 10px;
    border-radius: 3px;
  }

  .navbar-collapse .nav-link {
    color: #707070;
	padding-right: 20px;
  }
}

@media (max-width: 480px) {
  .tm-gallery-container {
    max-width: 220px;
    margin-left: auto;
    margin-right: auto;
  }

  .tm-gallery-container-2 {
    max-width: 350px;
  }

  .slick-dots li {
    margin: 0 8px;
  }

  .tm-gallery-item {
    margin: 0;
  }
}
