.slider_section {
    min-height: 560px;
    background: #595F72;
    position: relative;
}
.slider_section::after {
   width: 54%;
   position: absolute;
   top: 0;
   right: -370px;
   content: "";
   background: #0FA3B1;
   display: block;
   height: 100%;
   transform: skew(50deg);
}
.slider_section::before {
   width: 100%;
   position: absolute;
   bottom: 0;
   right: 0px;
   content: "";
   background: transparent;
   display: block;
   height: 20px;
}
.slider_section .full {
    position: relative;
    z-index: 9;
    color: aliceblue;
}
.slide_img {
   margin-top: 20%;
   margin-bottom: 0;
}
.top-banner-slider .heroslider {
    margin: 0px;
    padding: 0px;
    border: none;
    position: relative;
    border-radius: 0;
}
.swiper-overlay {
    height: 100%;
    position: relative;
}
.swiper-overlay {
    background: rgba(0, 0, 0, 0.5);
    content: "";
    width: 100%;
    height: 100%;
    left: 0px;
    top: 0px;
}

.slider_section .full {
    position: relative;
    z-index: 9;
}


.cur {
    color: #ffffff;
}

.slider_section h1 strong.cur {
