.card {
  margin: 25px;
  padding: auto;
  border-radius:15px
}

.card:hover {
    box-shadow: 5px 10px 20px 1px #284b63 !important;
    transition: all 0.7s linear;
    transform: scale(1.3);
  }
  
  .card-body {
    padding: 2rem 0 !important;
  }
  
  .card-text {
    padding: 0.3rem 1rem;
    font-size: 1rem;
  }
  
  .overflow {
    overflow: hidden;
  }
  
  .card-img-top {
    transform: scale(1);
    transition: all 0.2s ease-in-out;
  }
  
  .card-img-top:hover {
    transform: scale(1.3);
  }
